<template lang="pug">
  .news.inner-page
    .inner-page__main
      .news__add
        news-add(user)
      .news__list(v-if="getInfo")
        news-block(
          v-for="feed in getFeeds"
          :key="feed.id"
          :info="feed"
          :edit="getInfo.id === feed.author.id"
          :deleted="getInfo.id === feed.author.id"
          )
      infinite-loading(@infinite="onScroll")
    .inner-page__aside
      friends-possible
 </template>

<script>
import { mapGetters, mapActions } from 'vuex'
import FriendsPossible from '@/components/Friends/Possible'
import NewsBlock from '@/components/News/Block'
import NewsAdd from '@/components/News/Add'
import InfiniteLoading from 'vue-infinite-loading'

export default {
  name: 'News',
  components: { FriendsPossible, NewsBlock, NewsAdd, InfiniteLoading },
  data() {
    return {
      offset: 0,
      limit: 20,
      load: Boolean
    }
  },
  computed: {
    ...mapGetters('profile/feeds', ['getFeeds', 'getLimit', 'getOffset', 'getCount']),
    ...mapGetters('profile/info', ['getInfo'])
  },
  methods: {
    ...mapActions('profile/feeds', ['apiFeeds']),
    async onScroll($state) {
      this.offset = this.getOffset
      this.limit = this.getLimit
      await this.apiFeeds({
        offset: this.offset,
        limit: this.limit
      })
      if (+this.getCount === this.getFeeds.length){
        $state.complete()
      }
      $state.loaded()
    }
  },
  // beforeRouteEnter(to, from, next) {
  //   next(vm => {
  //     vm.apiFeeds()
  //   })
  // }
}
</script>

<style lang="stylus">
@import '../../assets/stylus/base/vars.styl';

.news__add {
  margin-bottom: 30px;
}
</style>



