import axios from 'axios'

export default {
  namespaced: true,
  state: {
    notifications: []
  },
  getters: {
    getNotifications: s => s.notifications,
    getNewNotificationsLength: s => s.notifications.filter(el => el.read_status === 'SENT').length,
    getNotificationsLength: s => s.notifications.length,
    getNotificationsTextType: s => type => {
      switch (type) {
        case 'POST':
          return 'опубликовал новую запись'
        case 'POST_COMMENT':
          return 'оставил комментарий'
        case 'COMMENT_COMMENT':
          return 'ответил на ваш комментарий'
        case 'FRIEND_REQUEST':
          return 'добавил в друзья нового пользователя'
        case 'FRIEND_BIRTHDAY':
          return 'день рождение'
        case 'MESSAGE':
          return 'прислал сообщение'
        case 'LIKE':
          return 'поставил лайк'
      }
    }
  },
  mutations: {
    setNotifications: (s, value) => s.notifications = value
  },
  actions: {
    async apiNotifications({
      state,
      commit,
      dispatch
    }) {
      await axios({
        url: 'notifications',
        method: 'GET'
      }).then(response => {
        if (`${state.notifications.map(z => z.sent_time + z.read_status)}` !== `${response.data.data.map(z => z.sent_time + z.read_status)}`) {
          commit('setNotifications', response.data.data)
        }
        // добавить когда будет прод
        // setTimeout(() => {
        //   dispatch('apiNotifications')
        // }, 5000)
      }).catch(() => {})
    },
    async readNotifications() {
      await axios({
        url: 'notifications?all=true',
        method: 'PUT'
      }).then(response => {}).catch(() => {})
    }
  }
}



